


























	import { Component, Vue, Watch, Prop } from "vue-property-decorator";
	import APPLICATION from "@/webrtc/APPLICATION";

	declare global {
		interface Window { videojs: any }
	}

	@Component
	export default class AudioStream extends Vue {
		static _socketServer = 'ws://' + process.env.VUE_APP_SERVER + ':' + process.env.VUE_APP_JANUS_PORT;
	
		static context  = new AudioContext();
		static analyser = AudioStream.context.createAnalyser();
		static frequency;
		static bufferLength;
		static source;
		static ctx;
		static WIDTH = 300; static HEIGHT = 100; static barWidth = 0;
		static canvas = null;

		touchEvent = 'ontouchstart' in window ? 'touchstart' : 'click';
        private servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
        };

		private player: HTMLAudioElement;
		private paused:boolean = false;
		private streamEnd:boolean = false;
		private audioPlayer: any;
		private stream: MediaStream;
		is_player_active = false;

		isPlaying: boolean = false;
		isMuted: boolean = false;

		sfuHandleA = null; janusA = null;
		global_timer_id;

		public mounted() {
			this.socketConnect();

			window.addEventListener('playRadioEvent', () => {
				this.isPlaying?	this._pause(): this._play();
			});
			window.addEventListener('muteRadioEvent', () => {
				this.isMuted?	this._unmute(): this._mute();
			});

			this.player = document.getElementById('radioPlayer') as HTMLMediaElement;
			this.player.onpause = () => this.isPlaying = false;
			this.player.onplaying = () => this.isPlaying = true;
		}


		registerAudioStream() {
			this.janusA.attach({
				plugin: 'janus.plugin.videoroom',
				success: (handle) =>{
					this.sfuHandleA = handle;
					var subscriber = { request: 'join', room: 2, ptype: 'subscriber', feed: 27053 };
					this.sfuHandleA.send({message: subscriber});
				},

				onmessage: (message, desc) =>{
					if(desc) this.sfuHandleA.createAnswer({
						jsep: desc, media: {audioSend: false, video: false, data: false, audioRecv: true },
						success: (jsep) => this.sfuHandleA.send({ message:{ request: 'start', room: 2}, jsep }),
						error: (error) => {}
					})
				},
				onremotestream: (stream) => {
					window.Janus.attachMediaStream(this.player, stream);
				}
			});
		}

		async _connectA(started) {
			if(started == 'yes') {
				this.streamEnd = false;
				window.Janus.init({
					debug: false, dependencies: window.Janus.useDefaultDependencies(), callback: () => {
						this.janusA = new window.Janus({
							server:  AudioStream._socketServer,
							iceServers: this.servers.iceServers,
							success: () => this.registerAudioStream(),
							error: (cause) => console.log('CAUSE cannt create JANU ' + cause)
						})	
					}
				});		
			}else{
				this.streamEnd = true;
			}
		}

		async _disconnectA() {
			// leave empty so radio can play in the background
			if(this.sfuHandleA != null)  this.sfuHandleA.send({message:{request: 'leave'}});
		}

		togglePlay() {
			this.isPlaying? this._pause(): this._play();
		}

		_play() {
			this.player.play();
		}

		_pause() {
			this.player.pause(); 
		}

		_mute() {
			this.player.muted = true;
			this.isMuted = true;
		}

		_unmute() {
			this.player.muted = false; 
			this.isMuted = false;
		}

		static init(media: MediaStream) {
			AudioStream.source = AudioStream.context.createMediaStreamSource(media);
			AudioStream.source.connect(AudioStream.analyser);
			AudioStream.analyser.connect(AudioStream.context.destination);

			AudioStream.frequency = new Uint8Array(AudioStream.analyser.frequencyBinCount);
			AudioStream.analyser.getByteFrequencyData(AudioStream.frequency);
			AudioStream.analyser.fftSize = 2048;
			AudioStream.bufferLength = AudioStream.analyser.frequencyBinCount;

		}

		static connect(media: MediaStream) {
			AudioStream.init(media);
			AudioStream.canvas = document.getElementById('canvas_webview') as HTMLCanvasElement;
			AudioStream.ctx = AudioStream.canvas.getContext('2d');
			//AudioStream.ctx.fillStyle = "black";
			AudioStream.canvas.height = 100;
			AudioStream.canvas.width  = 300;	
			AudioStream.barWidth = (AudioStream.WIDTH / AudioStream.bufferLength) * 0.5;
			AudioStream.renderFrame();
		}

		static renderFrame() {
			requestAnimationFrame(AudioStream.renderFrame);

			let barheight =0, x = 0;
			AudioStream.analyser.getByteFrequencyData(AudioStream.frequency);
			AudioStream.ctx.fillStyle = "black";
			AudioStream.ctx.fillRect(0,0, AudioStream.WIDTH, AudioStream.HEIGHT);

			let center = Math.round(AudioStream.WIDTH/2) - 1;
			for(var i =0; i < AudioStream.bufferLength; i++) {
				barheight = AudioStream.bufferLength[i] /2;
				AudioStream.ctx.fillStyle = 'red';
				AudioStream.ctx.fillRect(x, AudioStream.HEIGHT - barheight, AudioStream.barWidth, barheight);
				x = (center + (i % 2 == 0 ? -1 : 1) * Math.round(1 / 2));
			}
		}
	
		socketConnect() {
			let ssocket = new WebSocket(process.env.VUE_APP_WSS);

			ssocket.onopen = () => {
				if(this.global_timer_id != null) clearTimeout(this.global_timer_id); 
				ssocket.send('audio-started')
			}

			ssocket.onmessage = (m) => {
				m = JSON.parse(m.data);
				if(m['audiostarted']) this._connectA(m['audiostarted']);
			}

			ssocket.onclose = (c) => 
				this.global_timer_id = setTimeout(() => {
					this._pause();
					ssocket = null; this.socketConnect()
				}, 3000);
				
			ssocket.onerror = (e) => ssocket.close();
		}

}

